ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3 {
  font-size: 14px;

  & {
    margin-top: 0;
    margin-bottom: 0;
  }
}
p {
  margin-top: 0;
  margin-bottom: 0;
}
a {
  text-decoration: none;
  display: inline-block;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
